<template>
  <div class="rank">
    <div class="left">
      <div class="songtop">
        <el-image
          style="width: 250px; height: 250px"
          :src="soar.coverImgUrl"
        ></el-image>
        <div class="cover-info">
          <div class="cover-title">
            <h1 class="name">{{soar.name}}</h1>
            <span class="update-time">({{uptime}}更新)</span>
          </div>
          <div class="cover-user">
            <el-image
              style="width: 50px; height: 50px; border-radius: 50%"
              :src="soar.creator.avatarUrl"
            ></el-image>
            <span class="user-name">{{soar.creator.nickname}}</span>
            <span class="user-date">{{createTime}}</span>
          </div>
          <div class="cover-data">
            <span class="bofang"
              ><i class="iconfont icon-erji"></i>{{numberChange(soar.playCount)}}</span
            >
            <span class="shoucang"
              ><i class="iconfont icon-shoucang"></i>{{numberChange(soar.subscribedCount)}}</span
            >
            <span class="pinglu"
              ><i class="iconfont icon-24gl-bubbleDots2"></i>{{numberChange(soar.commentCount)}}</span
            >
          </div>
          <div class="cover-tags">
            <h3>歌单简介</h3>
            <span class="synopsis">{{soar.description}}</span>
          </div>
        </div>
      </div>
      <div class="songbottom">
        <div class="song-heard">
          <h4>歌曲列表<em>{{soar.tracks.length}}首歌</em></h4>
          <div>
            <span class="one"
              ><i class="iconfont icon-shoucang"></i>播放全部</span
            >
            <span class="two"><i class="iconfont icon-shoucang"></i>收藏</span>
          </div>
        </div>
        <songlist :songlist="getHotRankTracks"></songlist>
      </div>
    </div>
    <div class="right">
       <el-col :span="8">
      <el-dropdown trigger="click">
        <span class="el-dropdown-link">
          Dropdown List<el-icon class="el-icon--right"><arrow-down /></el-icon>
        </span>
        <template #dropdown>
          <el-dropdown-menu>
            <el-dropdown-item :icon="Plus">Action 1</el-dropdown-item>
            <el-dropdown-item :icon="CirclePlusFilled">
              Action 2
            </el-dropdown-item>
            <el-dropdown-item :icon="CirclePlus">Action 3</el-dropdown-item>
            <el-dropdown-item :icon="Check">Action 4</el-dropdown-item>
            <el-dropdown-item :icon="CircleCheck">Action 5</el-dropdown-item>
          </el-dropdown-menu>
        </template>
      </el-dropdown>
    </el-col>
    </div>
  </div>
</template>

<script>
import songlist from "./children/songlist.vue";
import dayjs from "dayjs";
import { useStore } from 'vuex';
import { computed, onMounted, reactive, ref } from '@vue/runtime-core';
export default {
  name: "Rank",
  components: {
    songlist,
  },
  setup() {
    const songInfo = reactive({
      parma:'19723756' 
    })
    const day = dayjs;
    const store = useStore()
    const soar = computed(() => {
      return store.state.home.homeRankList;
    });
    const getHotRankTracks = computed(() => {
      return store.getters.getHotRankTracks;
    });

    const uptime = ref(day(soar.value.updateTime).format("YYYY-MM-DD"));
    const createTime = ref(day(soar.value.createTime).format("YYYY-MM-DD"));
    
    const numberChange = (num) => {
      let num1 = String(num).split("");
      if (num1.length <= 4) {
        return num;
      } else {
        return num1.slice(0, num1.length - 4).join("") + "万";
      }
    };

    const init = () => {
      store.dispatch("getHotRankList", songInfo.parma)
    }

    onMounted(() => {
      init()
    })


    return{
      soar,
      getHotRankTracks,
      uptime,
      createTime,
      numberChange
    }
  },
};
</script>

<style lang="scss" scoped>
.rank {
  display: flex;
  .songtop {
    display: flex;
    .cover-info {
      flex: 1;
      display: flex;
      padding: 20px;
      text-align: left;
      margin-left: 20px;
      justify-content: space-between;
      flex-direction: column;
      .cover-title {
        display: flex;
        .update-time {
          line-height: 22px;
        }
      }
      .cover-user {
        display: flex;
        align-items: center;
      }
      .cover-data {
        .bofang {
          margin-left: 0;
        }
      }
      .cover-tags {
        .synopsis {
          margin: 5px 0 0 0;
        }
      }
    }
  }
  .songbottom {
    text-align: left;
    padding: 20px;
    margin-top: 20px;
    .song-heard {
      display: flex;
      justify-content: space-between;
      align-items: center;
      h4 {
        font-size: 20px;
        em {
          margin-left: 10px;
          font-size: 12px;
        }
      }
      span {
        padding: 10px;
        color: #000;
        border-radius: 50px;
        cursor: pointer;
      }
      .one {
        background-color: #f77700;
      }
      .two {
        background-color: #b9b9b9;
      }
    }
  }
  .right{
    flex:1
  }
  .left{
    flex: 3;
  }
}
span {
  display: inline-block;
  font-size: var(--text14);
  color: #909090;
  margin-left: 10px;
}
.cover-info,
.songbottom {
  border-radius: 10px;
  background: linear-gradient(145deg, #f6f6f6, #cfcfcf);
  box-shadow: 6px 6px 13px #bdbdbd, -6px -6px 13px #ffffff;
}
.el-image {
  border-radius: 10px;
}
.iconfont {
  margin-right: 5px;
}

</style>
